@use "global/variables" as v;
@use "global/mixins" as m;

.header {
  background-image: radial-gradient(
      circle at center,
      rgba(0, 0, 0, 0.55) 0%,
      /* centro m치s oscuro */ rgba(0, 0, 0, 0.7) 60%,
      /* transici칩n suave */ rgba(0, 0, 0, 0.85) 100% /* orillas oscuras */
    ),
    url("../img/hero-burger-AM592Wc6.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 100vh;
  color: v.$White;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  @include m.desktop {
    gap: 10rem;
  }
  .header__top {
    display: flex;
    justify-content: space-between;

    flex-direction: column;
    align-items: center;
    @include m.desktop {
      flex-direction: row;
    }

    .header__top--title {
      text-transform: uppercase;
      font-weight: 900;
      font-size: 3rem;
      .resaltar {
        color: v.$color-primario;
      }
    }
    .header__top--nav {
      display: flex;
      flex-direction: column;

      a {
        color: v.$White;
        font-weight: 500;
        &:hover {
          color: v.$color-primario;
        }
      }

      @include m.desktop {
        flex-direction: row;
        gap: 4rem;
      }
    }
  }

  .header__bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 2rem;
    h1 {
      width: 100%;
      text-align: center;
      text-transform: uppercase;
      font-weight: 900;

      @include m.desktop {
        font-size: 6rem;
      }
    }
    p {
      font-weight: 300;
      font-size: 2rem;
      text-align: justify;
    }

    .header__bottom--Button {
      display: flex;
      flex-direction: column;
      gap: 2rem;
      margin-top: 1rem;
      @include m.desktop {
        flex-direction: row;
      }
    }
  }
}
.menu-icon {
  width: 28px;
  height: 28px;
  filter: invert(100%);
  @include m.desktop {
    opacity: 0;
  }
  &:hover {
    cursor: pointer;
  }
}

.header__top--main {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  align-items: center;
}

.header__desplegable {
  background-color: v.$fondo-claro;
  width: 100%;
  overflow: hidden; // necesario para max-height
  max-height: 0;
  opacity: 0;
  transform: translateY(-10px);
  transition: max-height 0.4s ease, opacity 0.3s ease, transform 0.4s ease;

  padding: 0; // oculto cuando est치 cerrado
  display: flex;
  flex-direction: column;
  gap: 1rem;

  &.active {
    max-height: 500px; // aseg칰rate que sea suficiente para el contenido
    opacity: 1;
    transform: translateY(0);
    padding: 1rem;
  }

  @include m.desktop {
    max-height: 500px; // aseg칰rate que sea suficiente para el contenido
    opacity: 1;
    transform: translateY(0);
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background-color: transparent;
    padding: 1rem;
  }

  .header__top--nav {
    gap: 2rem;
  }
}
